*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat";
}

header {
  background-color: #010004;
}

.header-container {
  max-width: 1200px;
  margin: auto;
  padding: 0 10px;
}

.navbar {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

.header-grid {
  position: relative;
  min-height: 100px;
  display: grid;
  grid-template: "navbar-logo navbar-menu navbar-button";
}
@media (max-width: 768px) {
  .header-grid {
    grid-template: "navbar-logo . navbar-button" "navbar-menu navbar-menu navbar-menu";
  }
}
@media (max-width: 320px) {
  .header-grid {
    max-height: 103px;
    grid-template: "navbar-logo navbar-button navbar-mobile-menu-button";
  }
}

.navbar-logo {
  grid-area: navbar-logo;
  margin: auto;
  display: flex;
  align-items: center;
  cursor: default;
}
.navbar-logo:hover {
  color: #ffffff;
}

.navbar-logo__image-hover {
  display: none;
}

.navbar-log__images-wrapper {
  cursor: pointer;
  width: 43px;
  height: 42px;
}
.navbar-log__images-wrapper:hover > .navbar-logo__image {
  display: none;
}
.navbar-log__images-wrapper:hover .navbar-logo__image-hover {
  display: block;
}

.navbar-logo__text {
  cursor: pointer;
  color: #8D81AC;
  font-size: 24px;
  margin-left: 17px;
}
.navbar-logo__text:hover {
  color: #ffffff;
}
@media (max-width: 320px) {
  .navbar-logo__text {
    display: none;
  }
}

.navbar-container {
  grid-area: navbar-menu;
}
@media (max-width: 768px) {
  .navbar-container {
    padding: 0 16% 0 16%;
  }
}
@media (max-width: 432px) {
  .navbar-container {
    padding: 0 2% 0 2%;
  }
}
@media (max-width: 320px) {
  .navbar-container {
    display: none;
  }
}

.navbar-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.mobile-menu-container {
  display: none;
  position: absolute;
  z-index: 100;
  background: #333333f5;
  width: 90%;
  left: 4%;
  right: 5%;
}

.mobile-menu-container.open {
  display: block;
}

.mobile-menu {
  list-style: none;
  text-align: center;
  width: 100%;
  padding: 0;
  margin: 0;
}
@media (min-width: 321px) {
  .mobile-menu {
    display: none;
  }
}

.mobile-menu__item {
  color: white;
  height: 88px;
  display: flex;
  align-items: center;
  padding-left: 24%;
  border-left: 1px solid white;
}

.navbar-menu__items {
  grid-area: navbar-menu__items;
  text-decoration: none;
  color: #8D81AC;
  display: flex;
  flex-wrap: nowrap;
  cursor: pointer;
}
.navbar-menu__items:hover {
  color: #ffffff;
}

.navbar-button {
  grid-area: navbar-button;
  width: 145px;
  height: 38px;
  background: #8D81AC;
  border-radius: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  cursor: pointer;
}
@media (max-width: 320px) {
  .navbar-button {
    width: 119px;
    height: 35px;
  }
}

@media (max-width: 320px) {
  .navbar-mobile-menu-container {
    grid-area: navbar-mobile-menu-button;
  }
}

.navbar-mobile-menu-button {
  all: unset;
  display: none;
}
@media (max-width: 320px) {
  .navbar-mobile-menu-button {
    margin: 50% auto 50% auto;
    width: 40px;
    height: 40px;
    position: relative;
    display: block;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: 0.5s ease-in-out;
    -moz-transition: 0.5s ease-in-out;
    -o-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;
    cursor: pointer;
  }
}

@media (max-width: 320px) {
  .navbar-mobile-menu-button span {
    display: block;
    position: absolute;
    height: 4px;
    width: 100%;
    background: #ffffff;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: 0.25s ease-in-out;
    -moz-transition: 0.25s ease-in-out;
    -o-transition: 0.25s ease-in-out;
    transition: 0.25s ease-in-out;
  }
}

.navbar-mobile-menu-button span:nth-child(1) {
  top: 0px;
}

.navbar-mobile-menu-button span:nth-child(2) {
  top: 16px;
}

.navbar-mobile-menu-button span:nth-child(3) {
  top: 32px;
}

.navbar-mobile-menu-button.open span:nth-child(1) {
  top: 18px;
  -webkit-transform: rotate(135deg);
  -moz-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  transform: rotate(135deg);
}

.navbar-mobile-menu-button.open span:nth-child(2) {
  opacity: 0;
  left: -60px;
}

.navbar-mobile-menu-button.open span:nth-child(3) {
  top: 18px;
  -webkit-transform: rotate(-135deg);
  -moz-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg);
}

@media (max-width: 320px) {
  .mobile-menu-icon {
    display: block;
    margin: auto;
  }
}

